  /* Additional CSS styles for the welcome popup */
.welcome-section {
  position: fixed;
  top: 40%;
  left: 50%;
  width: 90vw;
  transform: translate(-50%, -50%);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  border-radius: 10px;
  max-width: 100vw;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
}

.welcome-section.show-popup {
  opacity: 1;
  visibility: visible;
}

/* Adjust this value to control how long the popup stays visible before animating to its original position */
.welcome-section.popup {
  animation: popupAnimation 3s forwards;
}

@keyframes popupAnimation {
  0% {
    top: 50%;
    opacity: 1;
    visibility: visible;
  }
  100% {
    top: 10%;
    opacity: 0;
    visibility: hidden;
  }
}

.okay-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.okay-button:focus {
  outline: none;
}



  
  