/* Add your custom CSS styles here */

.lesson-video-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .video-wrapper {
    position: relative;
    overflow: hidden;
    padding-top: 56.25%; /* Maintain 16:9 aspect ratio for the video */
  }
  
  .video-wrapper video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .views {
    font-size: 18px;
    margin-left: 10px;
  }
  